.App {
    text-align: center;
}

.App-logo {
    height: 80px;
}

.App-header {
    /*background-color: rgba(2, 104, 254, 0.45);*/
    padding-top: 70px;
    width: 50%;
    margin:auto;
}

.App-intro {
    font-size: large;
}

.serach-input {
    float: left;
    width: 500px;
}


.serach-button {
    margin-left: 100px;
    height: 30px;
    float: left;

}

@-webkit-keyframes App-logo-spin {
    from {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    to {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

@keyframes App-logo-spin {
    from {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    to {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}



